body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.header__wrapper {
  height: 500px;
  background-color: #9a69ff;
}

.header__menues {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 40px;
}

.header__main-title {
  font-size: 40px;
  color: white;
  font-family: fantasy;
}

.header__routes-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 50px;
  align-items: center;
}

.header__contact-us-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 20px;
  color: black;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
}

.header__route {
  color: white;
  font-size: 20px;
}
